.background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  overflow: hidden;
  background-size: 0;
}

.background::before,
.background::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: '';
}

.background::before {
  background: no-repeat center / cover;
  background-image: inherit;
  opacity: 1;
  -webkit-filter: none;
          filter: none;
  -webkit-transition: opacity 2.5s, -webkit-filter 2.5s, -webkit-transform 2.5s;
  transition: opacity 2.5s, -webkit-filter 2.5s, -webkit-transform 2.5s;
  transition: opacity 2.5s, filter 2.5s, transform 2.5s;
  transition: opacity 2.5s, filter 2.5s, transform 2.5s, -webkit-filter 2.5s, -webkit-transform 2.5s;
  -webkit-transform: none;
          transform: none;
}

.background.loading::before {
  opacity: 0;
  -webkit-filter: blur(5rem);
          filter: blur(5rem);
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
}

.background::after {
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4) 60%, rgba(0, 0, 0, 0.9)), 0 / 2px url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 4 4"><g style="fill:rgba(55,67,72,.9)"><rect x="0" y="0" width="2" height="2"/><rect x="2" y="2" width="2" height="2"/></g></svg>');
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4) 60%, rgba(0, 0, 0, 0.9)), 0 / 2px url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 4 4"><g style="fill:rgba(55,67,72,.9)"><rect x="0" y="0" width="2" height="2"/><rect x="2" y="2" width="2" height="2"/></g></svg>');
  /* transform: translateZ(0); */
}

