@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: 'Againts';
  src: url('../assets/fonts/againts.eot?') format('eot'), url('../assets/fonts/againts.woff') format('woff'), url('../assets/fonts/againts.ttf') format('truetype'), url('../assets/fonts/againts.svg#Againts') format('svg');
}

.title,
.slogan {
  font-family: Againts;
  opacity: 0;
  animation: fadeIn 1s ease forwards;
}

.title {
  margin: 0 0 1rem;
  color: #fff;
  text-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.8);
  font-size: 5rem;
  animation-delay: 0.25s;
}

.slogan {
  margin: 0 0 4rem;
  color: #eee;
  text-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.6);
  font-size: 2rem;
  animation-delay: 0.25s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(2rem) scale(0.8);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
